button {
	outline: none;
	border: none;
	background: none;
}
button:focus {
	outline:none;
}

/*GLOBAL BUTTON STYLES*/

.btn-container {font-size:0;}
.btn-container .btn {
    margin-right:1rem;
}
.btn-container.pull-right .btn {
    margin-left:1rem;
}
.btn {
    -moz-user-select:none;
    -webkit-appearance:none;
    cursor:pointer;
    display:inline-block;
    font-weight:700;
    line-height:1;
    text-align:center;
    border-radius:4px;
    text-decoration:none;
    position:relative;
    transition: all .3s ease;
    white-space:nowrap;
    overflow:hidden;
}
.btn:active { 
    background-image:none;
    outline:0;
    box-shadow:inset 0 3px 5px rgba(0,0,0,.125);
}
.btn-lg, .btn-group-lg > .btn {
    padding: 1rem 2.5rem;
    font-size: 1.6rem;
    height:3.8rem;
}
.btn-lg.btn-icon {
    width:3.8rem;
    padding:1.1rem;
}
.btn-md, .btn-group-md > .btn {
    padding: 1rem 2rem;
    font-size: 1.4rem;
    height:3.6rem;
}
.btn-md.btn-icon {
    width:3.6rem;
    padding:1rem;
}
.btn-sm, .btn-group-sm > .btn {
    padding: .8rem 1.5rem;
    font-size: 1.3rem;
    height:3rem;
}
.btn-sm.btn-icon {
    width:3rem;
    padding:.8rem;
}
.btn-sm.btn-icon .icon {
    width:1.3rem;
    height:1.3rem;
}
.btn-block {
    display:block;
    width:100%
}
.btn-primary, .btn-primary[disabled]:hover {
    color: #fff;
    border:1px solid #0C8346;
    background: #0C8346;
}
.btn-primary:hover {
    color:#fff;
}
.btn-cancel, .btn-cancel[disabled]:hover {
	border: 1px solid #888;
	color: #888;
	background: #fff;
}
.btn-secondary, .btn-secondary[disabled]:hover {
	border: 1px solid #1d67b2;
	color: #1d67b2;
	background: #fff;
}
.btn-primary .icon {fill:#fff;}
.btn-secondary .icon {fill:#1d67b2;}
.btn-cancel .icon {fill:#888;}
.btn:after {
    content:"";
    display:block;
    position:absolute;
    width:0;
    height:100%;
    left:0;
    top:0;
    opacity:0;
    border-radius:4px;
    background:rgba(0,0,0,.1);
    transition:opacity .3s ease;
}
.btn-secondary:after {
    background:rgba(20,101,181,.1);
}
.btn-primary:after {
    background:rgba(0,0,0,.2);
}
.btn[disabled]:after {
    display:none;
}
.btn:hover:after {
    width:100%;
    opacity:1;
}
.btn:last-of-type {
	margin: 0;
}
.btn:hover {
	text-decoration: none;
}
.btn:active {
	box-shadow: inset 0 0 1rem rgba(0,0,0,.2);
}
.btn[disabled] {
	opacity: .35;
}
.btn[disabled]:hover {
	cursor: not-allowed;
}
.btn-borderless, .btn-borderless:after {
    border:none;
    background:none;
}

/* BRAND CENTRIC STYLES */

.brand .btn-primary, .brand .btn-primary[disabled]:hover { 
    background:#1d67b2;
    color:#fff; 
    border:1px solid #1d67b2
}
.brand .btn-primary[disabled] {
    pointer-events:none;
    cursor:default;
    box-shadow:none;
    opacity:.5
}