/* Toggle Control Styles */
input[type="checkbox"].form-toggle + label {
    display: inline-block;
    position: relative;
    cursor: pointer;
    outline: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    padding: .2rem;
    width: 4rem;
    height: 2.3rem;
    background-color: #dddddd;
    border-radius: 6rem;
    margin:0;
}
input[type="checkbox"].form-toggle + label:before, input[type="checkbox"].form-toggle + label:after {
    display: block;
    position: absolute;
    top: .1rem;
    left: .1rem;
    bottom: .1rem;
    content: "";
}
input[type="checkbox"].form-toggle + label:before {
    right: 1px;
    background: #f1f1f1;
    border-radius: 6rem;
    transition: background 0.15s ease;
    border: 0 none;
}
input[type="checkbox"].form-toggle + label:after {
    width: 2rem;
    height:2rem;
    background: #fff;
    border-radius: 100%;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
    transition: margin 0.15s ease;
    opacity: 1;
    transform: scale(1);
}
input[type="checkbox"].form-toggle:checked + label, input[type="checkbox"].form-toggle:checked + label:before {
    background-color: #238723;
}
input[type="checkbox"].form-toggle:checked + label:after {
    margin-left: 1.8rem;
}
input[type="checkbox"].form-toggle:not(:checked) + label:before,
input[type="checkbox"].form-toggle:checked + label:before {
    width: auto; 
    height: auto;
}