input {
	padding: .5rem 1rem;
	border: 1px solid #ccc;
	height: auto;
	outline: none;
}
input::-ms-clear {
    display:none;
}

label, .label {
	display: block;
	font-weight: 700;
	color: #666;
	font-size: 1rem;
	letter-spacing: 1px;
	text-transform: uppercase;
	line-height: 1;
}
label span {
    color: #a20106;
}

form legend {font-weight:700;}
.form-control {
	display: block;
	height: 3.6rem;
    width: 100%;
	padding: .5rem 1rem;
	font-size: 1.4rem;
	line-height: 1.5;
	color: #222;
	background-color: #fff;
	background-image: none;
	border: 1px solid #ccc;
	border-radius: 4px;
	overflow: hidden;
    box-sizing:border-box;
	transition: border-color ease-in-out .15s, background-color ease-in-out .25s;
}
.form-control:hover, .input-group > *:last-child:hover {
    border-color: #87898c;
    background: #f3f8fc;
}
.form-control:focus, .input-group > *:last-child:focus {
    border-color: #1D67B2;
    box-shadow: 0 0 1px rgba(0, 0, 0, 0.075) inset;
    outline: 0 none;
    background: #f3f8fc;
}

textarea.form-control {
	height: auto;
}
form .help-block {
	color: #888;
	font-size: 1.2rem;
	margin: .5rem 0 0 0;
}
form .btn-group {
	display: table;
	clear: both;
	content: "";
}

.form-control[disabled], 
fieldset[disabled] .form-control {
    background-color: #f1f1f3;
    border: 1px solid #c6c9cc;
    box-shadow: none;
    color: #95989c;
    cursor: default;
    opacity: 0.8;
}
.form-control.error {
	border: 1px solid #c90107;
}
.error-message {
	font-size: 1.2rem;
	color: #fff;
	background: #c90107;
	padding: .5rem 1rem;
	font-weight: 700;
    display:inline-block;
}

/* Input Group */

.input-group {
    display:flex;
}
.input-group-addon {
  height:3.6rem;
  display:inline-block;
  padding:.65rem 1.5rem;
  text-align: center;
  background-color: #eee;
  border: 1px solid #ccc;
}
.input-group > * { 
    border-radius:0;
    border-right-color:transparent;
}
.input-group > *:first-child {
    border-radius:4px 0 0 4px;
}
.input-group > *:last-child {
    border-radius:0 4px 4px 0;
    border-color:#ccc;
}