.checkbox label, .radio label {
	color: #333;
	font-size: 1.4rem;
	text-transform: none;
	letter-spacing: 0;
}

.checkbox label, .radio label {
	font-weight: 400;
	line-height: 1.5;
}
.checkbox label input {
    position:relative; 
    top:.2rem;
}

input[type="radio"][disabled],
input[type="checkbox"][disabled],
input[type="radio"].disabled,
input[type="checkbox"].disabled,
fieldset[disabled] input[type="radio"],
fieldset[disabled] input[type="checkbox"] {
	cursor: not-allowed;
	color: #aaa;
}
.radio-inline.disabled,
.checkbox-inline.disabled,
fieldset[disabled] .radio-inline,
fieldset[disabled] .checkbox-inline {
	cursor: not-allowed;
	color: #aaa;
}
.radio.disabled label,
.checkbox.disabled label,
fieldset[disabled] .radio label,
fieldset[disabled] .checkbox label {
	cursor: not-allowed;
	color: #aaa;
}


input[type="checkbox"]:not(:checked),
input[type="checkbox"]:checked,
input[type="radio"]:not(:checked),
input[type="radio"]:checked {
    display: none;
}
input[type="checkbox"]:not(:checked) + label,
input[type="checkbox"]:checked + label,
input[type="radio"]:not(:checked) + label,
input[type="radio"]:checked + label {
    position: relative;
    padding-left: 2.5rem;
    cursor: pointer;
    line-height:1.4;
    display: inline-block;
}
input[type="checkbox"]:not(:checked) + label:before,
input[type="checkbox"]:checked + label:before,
input[type="radio"]:not(:checked) + label:before,
input[type="radio"]:checked + label:before {
    content: '';
    position: absolute;
    left:0; 
    top: 0;
    width: 1.8rem; 
    height: 1.8rem;
    border: 1px solid #aaa;
    background: #fff;
    border-radius: 4px;
}
input[type="checkbox"]:not(:checked) + label:after,
input[type="checkbox"]:checked + label:after {
    content: '';
    width: 2rem; 
    height: 2rem;
    background: url("data:image/svg+xml,%3Csvg viewBox='0 0 18 18' width='18px' height='18px' xmlns='http://www.w3.org/2000/svg' fill-rule='evenodd' clip-rule='evenodd' stroke-linejoin='round' stroke-miterlimit='1.414'%3E%3Cpath d='M18 3.498L9.23 14.255l-2 2.613-2.46-2.152L0 10.876 2.154 8.26l4.615 3.996L15.384 1.5 18 3.498z' fill='%23238723'/%3E%3C/svg%3E") no-repeat .1rem .1rem;
    background-size:1.6rem 1.6rem;
    position: absolute;
    left:0; 
    top: 0;
    transition: all .2s ease;
}
input[type="checkbox"]:disabled:checked + label:after {
    background: url("data:image/svg+xml,%3Csvg viewBox='0 0 18 18' width='18px' height='18px' xmlns='http://www.w3.org/2000/svg' fill-rule='evenodd' clip-rule='evenodd' stroke-linejoin='round' stroke-miterlimit='1.414'%3E%3Cpath d='M18 3.498L9.23 14.255l-2 2.613-2.46-2.152L0 10.876 2.154 8.26l4.615 3.996L15.384 1.5 18 3.498z' fill='%23434343'/%3E%3C/svg%3E") no-repeat .1rem .1rem;
    background-size:1.6rem 1.6rem;
    opacity:.5;
}

/* Radio Styles */
input[type="radio"]:not(:checked) + label:before,
input[type="radio"]:checked + label:before {
    border-radius:50%;
    width: 1.8rem; 
    height: 1.8rem;
}

input[type="radio"]:not(:checked) + label:after,
input[type="radio"]:checked + label:after {
    content: '';
    position: absolute;
    top: .1rem; 
    left: .2rem;
    font-size: 1.6rem;
    line-height: 1;
    color: #238723;
    transition: all .2s ease;
}
input[type="radio"]:not(:checked) + label:after,
input[type="radio"]:checked + label:after {
    content:''; 
    width:1rem;
    height:1rem;
    display:block;
    border-radius:50%;
    background:#238723; 
    top: .4rem;
    left: .4rem;
}

input[type="radio"]:disabled:checked + label:after {
    background:#999;
}

input[type="checkbox"]:not(:checked) + label:after,
input[type="radio"]:not(:checked) + label:after {
    opacity: 0;
    transform: scale(1.5);
}
input[type="checkbox"]:checked + label:after,
input[type="radio"]:checked + label:after {
    opacity: 1;
    transform: scale(1);
}

input[type="checkbox"]:disabled:not(:checked) + label:before,
input[type="checkbox"]:disabled:checked + label:before,
input[type="radio"]:disabled:not(:checked) + label:before,
input[type="radio"]:disabled:checked + label:before {
    box-shadow: none;
    border-color: #bbb;
    background-color: #ddd;
}
input[type="checkbox"]:disabled:checked + label:after,
input[type="radio"]:disabled:checked + label:after {
    color: #999;
}
input[type="checkbox"]:disabled + label, 
input[type="radio"]:disabled + label {
    color: #aaa;
}