/*GLOBAL BADGE STYLES*/

.badge {
    font-weight: 700;
    text-align:center;
    white-space: nowrap;
    border-radius: 2rem;
    display:inline-block;
    text-transform: uppercase;
    margin-right:.5rem;
    width:auto;
    line-height:1;
    vertical-align: middle;
}
.badge-sd {
    border:none;
    background:#434343;
    color:#fff;
}
.badge-ol {
    color: #333;
    border: 1px solid #434343;
    background:none;
}
.badge-sm {
    font-size: .9rem;    
    padding: .6rem 1rem;
}
.badge-md {
    font-size:1.1rem;
    padding: .6rem 1.5rem;
}
.badge-lg {
    font-size:1.3rem;
    padding: .6rem 2rem;
}
.badge-ol.badge-yellow {border-color:#FFBF00;color:#434343;}
.badge-ol.badge-red {border-color:#C90107;color:#C90107;}
.badge-ol.badge-blue {border-color:#1D67B2;color:#1D67B2;}
.badge-ol.badge-green {border-color:#238723;color:#238723;}
.badge-sd.badge-yellow {background-color:#FFBF00;color:#434343;}
.badge-sd.badge-red {background-color:#C90107;}
.badge-sd.badge-blue {background-color:#1D67B2;}
.badge-sd.badge-green {background-color:#238723;}