h1, h2, h3, h4, h5, h6 {
	margin: 0 0 1rem 0;
	padding: 0;
	font-weight: 300;
}
h1 {
	font-size: 3.2rem;
	font-weight: 300;
}
h2 {
	font-size: 2.4rem;
	font-weight: 400;
}
h3 {
	font-size: 1.8rem;
	font-weight: 700;
}
h4 {
	font-size: 1.6rem;
	font-weight: 700;
}
h5 {
	font-size: 1.5rem;
	font-weight: 700;
}
h6 {
	font-size: 1.4rem;
	font-weight: 700;
}
ul, li {
	padding: 0;
	margin: 0;
	list-style-type: none;
}
p {
	font-size: 1.4rem;
	line-height: 1.4;
    margin:0 0 1rem 0;
}
p:last-child {
    margin:0;
}
a {
	text-decoration: none;
	cursor: pointer;
	color: #1D67B2;
	transition: color .25s ease;
}
a:hover {
	text-decoration: underline;
}